@import url("https://fonts.googleapis.com/css2?family=Sora&display=swap");

body {
  font-family: "Sora", sans-serif;
}
.item-button button{
 margin-top: 20px;
 width: 200px;
 background-color: rgba(0, 128, 0, 0.954);
 color: white;
}
.spinner{
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50% );
  z-index: 100;
}
.spinner-border{
  height: 100px;
  width: 100px;
}
.category{
  border: 2px solid gray;
  margin-right: 25px;
  margin-bottom: 10px;
  padding: 10px;
  cursor: pointer;
}
.category img{
  margin-left: 10px;

}
.category-active{
  border: 2px solid green;
  background-color: rgba(128, 128, 128, 0.534);
}

.register{
  display: flex;
  
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}
.register input{
  width: 400px !important;
}
.regsiter-form{
  background-color: rgba(66, 158, 244, 0.313);
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px; 
}
.regsiter-form h1{
  text-align: center;
  background-color: rgb(3, 3, 83);
  color: white;
  padding: 10px;
}
.regsiter-form h3{
  color: rgb(8, 8, 122);
  text-align: center;
}